"use client";

import { useState } from 'react';
import { Button } from "@/components/ui/button";
import { Input } from "@/components/ui/input";
import { Card } from "@/components/ui/card";
import { Alert } from "@/components/ui/alert";
import { Toaster, toast } from 'react-hot-toast';
import { generateChapterContent, createWordDocument } from '@/lib/report-generator';

export default function Home() {
  const [title, setTitle] = useState('');
  const [loading, setLoading] = useState(false);
  const [currentChapter, setCurrentChapter] = useState(0);
  const [generatedChapters, setGeneratedChapters] = useState<any[]>([]);

  const generateReport = async () => {
    if (!title) {
      toast.error('Please enter a project title');
      return;
    }

    setLoading(true);
    try {
      const chapters = [];
      
      // Generate chapters 1-3 sequentially
      for (let chapter = 1; chapter <= 3; chapter++) {
        setCurrentChapter(chapter);
        const toastId = toast.loading(`Generating Chapter ${chapter}...`);
        
        try {
          const chapterContent = await generateChapterContent(title, chapter);
          chapters.push(chapterContent);
          setGeneratedChapters([...chapters]);
          toast.success(`Chapter ${chapter} generated!`, { id: toastId });
        } catch (error: any) {
          toast.error(error.message || `Failed to generate Chapter ${chapter}`, { id: toastId });
          throw error;
        }
      }

      // Create and download Word document
      const toastId = toast.loading('Creating Word document...');
      try {
        const docBuffer = await createWordDocument(title, chapters);
        const blob = new Blob([docBuffer], { 
          type: 'application/vnd.openxmlformats-officedocument.wordprocessingml.document' 
        });
        const url = window.URL.createObjectURL(blob);
        const link = document.createElement('a');
        link.href = url;
        link.download = `${title.replace(/\s+/g, '_')}_report.docx`;
        document.body.appendChild(link);
        link.click();
        document.body.removeChild(link);
        window.URL.revokeObjectURL(url);
        toast.success('Report downloaded successfully!', { id: toastId });
      } catch (error: any) {
        toast.error('Failed to create Word document', { id: toastId });
        throw error;
      }

    } catch (error: any) {
      console.error('Error generating report:', error);
      if (error.message?.includes('API key')) {
        toast.error('OpenAI API key is not configured. Please check server configuration.');
      } else {
        toast.error('Failed to generate complete report. Please try again.');
      }
    } finally {
      setLoading(false);
      setCurrentChapter(0);
    }
  };

  return (
    <main className="min-h-screen bg-gray-50 py-8">
      <Toaster position="top-center" />
      <div className="container mx-auto px-4">
        <Card className="max-w-4xl mx-auto p-8 bg-white shadow-lg">
          <div className="text-center mb-8">
            <h1 className="text-4xl font-bold mb-2">
              Final Year Project Report Generator
            </h1>
            <p className="text-gray-600">
              Generate a well-documented report for your final year project using AI
            </p>
          </div>
          
          <div className="space-y-6">
            <div>
              <label htmlFor="title" className="block text-sm font-medium mb-2 text-gray-700">
                Project Title
              </label>
              <Input
                id="title"
                value={title}
                onChange={(e) => setTitle(e.target.value)}
                placeholder="Enter your project title"
                className="w-full"
                disabled={loading}
              />
            </div>

            <Button
              onClick={generateReport}
              disabled={loading || !title}
              className="w-full py-6 text-lg font-semibold bg-black hover:bg-gray-800 text-white"
            >
              {loading ? `Generating${currentChapter ? ` Chapter ${currentChapter}` : ''}...` : 'Generate Report'}
            </Button>

            {generatedChapters.length > 0 && (
              <div className="mt-8 space-y-4">
                <h2 className="text-xl font-semibold mb-4">Generated Chapters:</h2>
                {generatedChapters.map((chapter, index) => (
                  <Card key={index} className="p-4 border border-gray-200">
                    <h3 className="font-medium mb-2 text-lg">{chapter.title}</h3>
                    <div className="text-sm text-gray-600 line-clamp-3">
                      {chapter.content[0]}
                    </div>
                  </Card>
                ))}
              </div>
            )}
          </div>
        </Card>
      </div>
    </main>
  );
}
